/* 表格 */
import './index.less';
import React from 'react';
import { HashRouter as Router, Route, withRouter } from 'react-router-dom';
import { RoutersConfig } from 'router/index.jsx';
import { Divider, Button, Tooltip } from 'antd';

class Tables extends React.Component {
  pathDeal = (path) => {
    if (path.indexOf(':') > 0) {
      return path.substring(0, Number(path.indexOf(':'))-1);
    }
    return path;
  };
  componentDeal = (component, path) => {
    return path.length>1 ? component.substring(0, path.length) : component;
  };
  render() {
    let node = RoutersConfig.find(item => {
      return this.componentDeal(this.props.match.path, this.pathDeal(item.path)) === this.pathDeal(item.path);
    });
    let child = node.children.find(item => {
      return this.componentDeal(this.props.location.pathname, this.pathDeal(item.path)) === this.pathDeal(item.path);
    });
    let component = child.children.find(item => {
      return this.props.location.pathname === item.path
    })
    return (
      <div className="tables">
        <Divider orientation="left">
          <Tooltip placement="topLeft" title='点击查看官方文档'>
            <Button type="link" onClick={ () => window.open('https://ant.design/components/table-cn/', '_blank') }>antd-表格组件</Button>
          </Tooltip>
        </Divider>
        <Router>
          <Route path={ component.path }
            component={ component.component }/>
        </Router>
      </div>
    );
  }
}

export default withRouter(Tables);
